@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  box-sizing: border-box;
}
:root {
  --bgDark: #282a34;
  --bgSecondaryDark: #3c3f51;
  --bgTertiaryDark: #5e627e;
  --themePink: #e2336b;
  --themeYellow: #fcac46;
  --themeWhite: #ffffff;
}
body {
  background-color: #3c3f51;
  background-color: var(--bgSecondaryDark);
  color: #ffffff;
  color: var(--themeWhite);
}
.bg-ThemeDark {
  background-color: #282a34 !important;
  background-color: var(--bgDark) !important;
}
.bg-ThemeSecondaryDark {
  background-color: #3c3f51 !important;
  background-color: var(--bgSecondaryDark) !important;
}
.bg-ThemePink {
  background-color: #e2336b !important;
  background-color: var(--themePink) !important;
}
.bg-ThemeYellow {
  background-color: #fcac46 !important;
  background-color: var(--themeYellow) !important;
}
.bg-ThemeTertiary {
  background-color: #5e627e !important;
  background-color: var(--bgTertiaryDark) !important;
}
.text-ThemeWhite {
  color: #ffffff !important;
  color: var(--themeWhite) !important;
}
.text-ThemeSecondaryDark {
  color: #3c3f51 !important;
  color: var(--bgSecondaryDark) !important;
}
.text-ThemeTertiaryDark {
  color: #5e627e !important;
  color: var(--bgTertiaryDark) !important;
}
.text-ThemePink {
  color: #e2336b !important;
  color: var(--themePink) !important;
}
.border-ThemePink {
  border-color: #e2336b !important;
  border-color: var(--themePink) !important;
}
.border-ThemeSecondaryDark {
  border-color: #3c3f51 !important;
  border-color: var(--bgSecondaryDark) !important;
}
.w-4 {
  width: 4rem !important;
}
.w-3 {
  width: 3rem !important;
}
.w-1 {
  width: 1rem !important;
}
.w-2 {
  width: 2rem !important;
}
.w-5 {
  width: 5rem !important;
}
.w-6 {
  width: 6rem !important;
}
.w-16 {
  width: 16rem !important;
}
.h-4 {
  height: 4rem !important;
}
.h-3 {
  height: 3rem !important;
}
.h-1 {
  height: 1rem !important;
}
.h-2 {
  height: 2rem !important;
}
.h-5 {
  height: 5rem !important;
}
.h-6 {
  height: 6rem !important;
}

.box-sizing-unset {
  box-sizing: unset !important;
}
.rounded-4 {
  border-radius: 1rem !important;
}
.p-01 {
  padding: 0.1rem !important;
}
.w-icon-25 {
  width: 25px !important;
}
.h-icon-25 {
  height: 25px !important;
}
.text-small {
  font-size: small;
}
.text-x-small {
  font-size: x-small;
}

.content {
  overflow: auto;
  height: 100vh;
}



.Sidebar {
  height: 100vh;
  overflow: scroll;
}
::-webkit-scrollbar {
  display: none;
}

.search-input {
  background-color: transparent;
  border: none;
  outline: none;
}

.PostButton {
  background-image: linear-gradient(
    to right,
    #e23363,
    #f15256,
    #fa714c,
    #fd8f46,
    #fcac46
  );
}

.Stories {
  overflow: scroll;
  width: 100vw;
}
.stories-button {
  background-color: transparent;
  outline: none;
  border: none;
}

.FeedCard__label {
  font-size: x-small;
}

